.button {
  font-family: 'Anonymous Pro', monospace;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  padding: 10px 15px 10px 13px;
  vertical-align: middle;
  -webkit-appearance: initial;
  border: none; }
  .button:active, .button.active {
    padding-top: 12px;
    padding-bottom: 8px; }

.button-medium {
  font-family: 'Anonymous Pro', monospace;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  padding: 7px 13px 6px;
  line-height: 1; }
  .button-medium:active, .button-medium.active {
    position: relative;
    top: 2px;
    padding-top: 7px;
    padding-bottom: 5px; }

.button-small {
  display: inline-block;
  font-weight: bold;
  font-size: 13px;
  font-family: sans-serif;
  padding: 5px 8px;
  text-align: center; }
  @media (min-width: 1025px) {
    .button-small {
      min-width: 25px;
      min-height: 25px; } }

.button-inset {
  display: inline-block;
  font-weight: bold;
  font-size: 13px;
  font-family: sans-serif;
  padding: 5px 8px;
  text-align: center; }
  @media (max-width: 1024px) {
    .button-inset.minimize {
      padding: 8px 10px 5px; } }

