html,
body {
  font-family: 'Anonymous Pro', monospace; }
  @media (min-width: 1025px) {
    html,
    body {
      width: 100%;
      height: 100%;
      overflow: hidden; } }

html {
  background: #000; }

.shutDownPage {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0;
  transition: opacity .2s ease-in-out;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center; }
  .shutDownPage.visible {
    opacity: 1;
    pointer-events: auto;
    z-index: 5; }

.desktop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  @media (min-width: 1025px) {
    .desktop {
      position: static;
      height: 100vh; } }
  .desktop .icons {
    position: absolute;
    top: 38px;
    left: 45px; }
    .desktop .icons a,
    .desktop .icons button {
      text-decoration: none;
      text-align: center;
      display: block;
      margin-bottom: 50px;
      color: #fff;
      text-shadow: 1px 2px 0 rgba(0, 0, 0, 0.5); }
    .desktop .icons img {
      width: 60px;
      display: block;
      margin-bottom: 15px; }

.program {
  position: absolute;
  width: 100%;
  cursor: default;
  padding-bottom: 9px;
  display: inline-block; }
  @media (max-width: 1024px) {
    .program {
      transform: none !important; } }
  @media (min-width: 1025px) {
    .program {
      max-width: 100%;
      width: auto; } }
  .program:not(.notification) {
    height: 100%; }
    @media (min-width: 1025px) {
      .program:not(.notification) {
        height: auto; } }
  .program:not(.system) .content {
    background: #fff; }
  .program .content {
    margin-top: 6px;
    width: calc(100% - 13px);
    padding: 10px;
    margin-left: 3px;
    height: calc(100% - 44px); }
    @media (min-width: 750px) {
      .program .content {
        height: calc(100% - 96px); } }
    @media (min-width: 1025px) {
      .program .content {
        height: auto; } }
    .program .content[contentEditable=true] {
      overflow: scroll; }
      @media (min-width: 1025px) {
        .program .content[contentEditable=true] {
          max-height: 250px; } }
    .program .content h3 {
      margin-bottom: 10px;
      line-height: 1.2; }
  .program.previous-active {
    z-index: 1; }
  .program.active {
    z-index: 2; }

[data-view="closed"],
[data-view="minimized"] {
  display: none; }

@media (min-width: 1025px) {
  .txt-file {
    width: 400px; }
    .txt-file .content {
      max-height: 450px; } }

.txt-file .content {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  line-height: 1.3; }

.work.program .content div:not(:last-child) {
  margin-bottom: 30px; }
